@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-style: normal;
  font-weight: bold;
}

/*color*/
@blue: #81b3d2;
@blue-hover: #669ec0;
@blue-active: #5496bd;
@grey: #f2f2f2;
@grey-hover: #e5e5e5;
@grey-active: #d5d5d5;
@grey-notactive: #a4a4a4;

@tablet-width: 768px;
@desktop-width: 1200px;
@mobile-width-only: 767px;
@retina-dpi: 144dpi;
@retina-dppx: 1.5dppx;
